@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.ttf");
  src: local("Roboto"), local("Roboto"), url("../fonts/Roboto-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-VariableFont_wght.ttf");
  src: local("Montserrat"), local("Montserrat"), url("../fonts/Montserrat-VariableFont_wght.ttf") format("truetype");
  font-weight: 100 900;
  font-style: normal;
}
.button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0;
  background-color: transparent;
  border-width: 1px;
  border-style: solid;
  border-color: #fff;
  border-radius: 12px;
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.2s;
}
.button.l {
  padding: 11px 31px;
  font-size: 16px;
  line-height: 24px;
}
.button.m {
  padding: 13px 31px;
  font-size: 14px;
  line-height: 20px;
}
.button.primary {
  border-color: rgb(255, 107, 0);
  background-color: rgb(255, 107, 0);
  color: rgba(255, 255, 255, 0.92);
}
.button.primary:hover:not(:disabled) {
  border-color: rgb(224, 94, 0);
  background-color: rgb(224, 94, 0);
}
.button.secondary {
  border-color: rgba(255, 255, 255, 0.72);
  color: rgba(255, 255, 255, 0.72);
}
.button.secondary:hover:not(:disabled) {
  background-color: rgba(255, 255, 255, 0.92);
  border-color: rgba(255, 255, 255, 0.92);
  color: rgb(39, 42, 42);
}

.header {
  z-index: 900;
  position: fixed;
  top: 16px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 24px;
  max-width: 1152px;
  height: 88px;
  display: flex;
  align-items: center;
  padding: 16px 24px;
  background-color: #27282a;
}
.header__logo-block {
  height: fit-content;
  display: flex;
  align-items: flex-end;
}
.header__logo-text {
  margin-left: 10px;
  margin-bottom: 5px;
}
.header__auth {
  flex-shrink: 0;
  margin-left: 48px;
}

.nav {
  display: flex;
  flex-direction: row;
  margin-left: 48px;
  padding-left: 48px;
}
.nav__list {
  display: flex;
  padding: 0;
  margin: 0;
  list-style: none;
}
.nav__element {
  display: flex;
}
.nav__link {
  padding: 16px 24px;
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 600;
  color: #fff;
  text-decoration: none;
  cursor: pointer;
  text-wrap: nowrap;
  transition: color 0.2s;
}
.nav__link:hover {
  color: #e05e00;
}

.footer {
  background-color: rgb(235, 242, 251);
}
.footer__wrapper {
  max-width: 1152px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  gap: 48px;
  margin: 24px auto;
  padding: 24px;
  border-radius: 24px;
  background-color: rgb(39, 40, 42);
}
.footer__logo-block {
  padding-left: 24px;
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.footer__icon-wrapper {
  height: fit-content;
  display: flex;
  align-items: flex-end;
}
.footer__logo-text {
  margin-left: 10px;
  margin-bottom: 5px;
}
.footer__logo-text {
  font-size: 14px;
  line-height: 20px;
  color: rgba(255, 255, 255, 0.72);
}
.footer__nav-block {
  display: flex;
  gap: 48px;
}
.footer__list {
  padding: 8px 0 12px 24px;
  border-left: 1px solid rgba(255, 255, 255, 0.24);
}
.footer__list ul {
  max-width: 216px;
  display: flex;
  flex-direction: column;
  list-style: none;
  gap: 12px;
  padding: 0;
  margin: 0;
}
.footer__list a {
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: rgba(255, 255, 255, 0.92);
  text-decoration: none;
  transition: color 0.2s;
}
.footer__list a:hover {
  color: rgb(255, 107, 0);
}

.main {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.preview {
  min-height: 100vh;
  padding-top: 144px;
}

.content {
  margin-left: auto;
  margin-right: auto;
  max-width: 1152px;
}

.preview-content {
  position: relative;
  display: flex;
  padding: 66px 0;
}
.preview-content__content {
  position: relative;
  z-index: 1;
  width: 752px;
  display: flex;
  flex-direction: column;
  gap: 24px;
  margin-left: auto;
}
.preview-content__img {
  position: absolute;
  z-index: 0;
  top: -1px;
  left: -80px;
  width: 1128px;
  height: 525px;
  object-fit: cover;
  pointer-events: none;
}
.preview-content__list {
  display: flex;
  gap: 4px;
}
.preview-content__list-element {
  min-height: 52px;
  padding: 12px 24px;
  border-radius: 999px;
  border: 1px solid rgba(255, 255, 255, 0.24);
  background-color: rgba(0, 0, 0, 0.24);
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 28px;
  color: rgba(255, 255, 255, 0.92);
}
.preview-content__text {
  display: flex;
  flex-direction: column;
  gap: 12px;
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
  color: rgba(255, 255, 255, 0.92);
}
.preview-content__text-title {
  margin: 0;
  font-size: 48px;
  line-height: 64px;
}
.preview-content__footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  max-width: 385px;
  padding-top: 48px;
}
.preview-content__footer-label {
  font-size: 14px;
  line-height: 20px;
  color: rgba(255, 255, 255, 0.72);
}

.main-text {
  margin-top: 72px;
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 56px;
  text-align: center;
  color: rgba(255, 255, 255, 0.92);
}

.messages {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 48px;
  margin-bottom: -48px;
}
.messages__circle-s {
  position: absolute;
  top: 96px;
  left: 50%;
  transform: translateX(-50%);
  width: 800px;
  height: 800px;
}
.messages__circle-m {
  position: absolute;
  top: 0px;
  left: 50%;
  transform: translateX(-50%);
  width: 992px;
  height: 992px;
}
.messages__avatars-list {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 864px;
  list-style: none;
  padding: 0;
  margin: 0;
}
.messages__avatar-element {
  position: absolute;
}
.messages__avatar-element:nth-child(1) {
  left: 62px;
  top: 182px;
}
.messages__avatar-element:nth-child(2) {
  left: 146px;
  top: -6px;
}
.messages__avatar-element:nth-child(3) {
  left: 402px;
  top: 37px;
}
.messages__avatar-element:nth-child(4) {
  left: 612px;
  top: 2px;
}
.messages__avatar-element:nth-child(5) {
  left: 670px;
  top: 172px;
}
.messages__avatar-button {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 120px;
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 50%;
  background-color: transparent;
  cursor: pointer;
}
.messages__avatar-button.selected {
  border: 1px solid rgb(255, 107, 0);
}
.messages__avatar-img {
  width: 96px;
  height: 96px;
  border-radius: 50%;
}
.messages__avatar-shadow {
  position: absolute;
  left: -9px;
  top: -9px;
  width: 136px;
  height: 136px;
  object-fit: contain;
}
.messages__buttons-list {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  max-width: 864px;
  width: 100%;
  padding: 0;
  margin: 240px auto 64px;
  list-style: none;
}
.messages__buttons-list-block {
  display: flex;
  gap: 8px;
}
.messages__button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 148px;
  height: 52px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 99px;
  background-color: rgba(255, 255, 255, 0.08);
  color: rgba(255, 255, 255, 0.92);
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 28px;
  cursor: pointer;
}
.messages__button.selected {
  border-color: rgb(255, 107, 0);
}

.slider {
  z-index: 10;
  display: flex;
  flex-direction: column;
  gap: 16px;
  width: 864px;
  padding: 48px 48px 24px 32px;
  border-radius: 24px;
  background-color: rgb(255, 255, 255);
  box-shadow: 0 16px 64px 0 rgba(0, 0, 0, 0.08), 0 0 4px 0 rgba(0, 0, 0, 0.04);
}
.slider__slide {
  display: flex;
  gap: 24px;
}
.slider__controls {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.slider__controls-button {
  height: 48px;
  display: flex;
  align-items: center;
  border: none;
  background-color: transparent;
  padding: 0;
  margin: 0;
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: rgb(11, 87, 208);
  cursor: pointer;
  transition: color 0.2s;
}
.slider__controls-button:hover {
  color: rgb(224, 94, 0);
}
.slider__controls-button:hover .slider__controls-button-icon path {
  fill: rgb(224, 94, 0);
}
.slider__controls-button-icon {
  width: 32px;
  height: 32px;
}
.slider__controls-button-icon path {
  transition: fill 0.2s;
}
.slider__pagination {
  display: flex;
  gap: 12px;
}
.slider__pagination-item {
  position: relative;
  width: 8px;
  height: 8px;
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.16);
  cursor: pointer;
}
.slider__pagination-item::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 20px;
}
.slider__pagination-item.selected {
  background-color: rgb(255, 107, 0);
}

.slide__img-block {
  width: 192px;
  min-height: 304px;
  position: relative;
}
.slide__img {
  position: absolute;
  left: 0;
  top: -72px;
  width: 192px;
  height: 376px;
}
.slide__content {
  display: flex;
  flex-direction: column;
}
.slide__title {
  margin-bottom: 12px;
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
}
.slide__sub-title {
  margin-bottom: 32px;
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
}
.slide__list {
  counter-reset: custom-counter;
  display: flex;
  flex-direction: column;
  gap: 12px;
  list-style: none;
  padding: 0;
  margin: 0;
}
.slide__list-element {
  counter-increment: custom-counter;
  position: relative;
  padding-left: 36px;
}
.slide__list-element::before {
  content: counter(custom-counter);
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  background-color: rgba(255, 107, 0, 0.16);
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
}

.functions {
  padding-top: 96px;
  padding-bottom: 48px;
  background-color: rgb(255, 255, 255);
}
.functions__title {
  margin: 0 0 12px;
  text-align: center;
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 700;
  font-size: 28px;
  line-height: 36px;
  color: rgb(39, 42, 42);
}
.functions__text {
  margin: 0 auto 28px;
  max-width: 510px;
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 28px;
  text-align: center;
}
.functions__link {
  height: 48px;
  display: block;
  width: fit-content;
  margin: 0 auto;
  padding: 12px 0;
  text-decoration: none;
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 600;
  color: rgb(11, 87, 208);
  transition: color 0.2s;
}
.functions__link:hover {
  color: rgb(224, 94, 0);
}
.functions__content {
  display: flex;
  align-items: flex-end;
  gap: 24px;
}
.functions__content-block {
  width: 270px;
  display: flex;
  flex-direction: column;
  padding: 24px;
  border-radius: 24px;
}
.functions__content-block:nth-child(1) {
  height: 400px;
  background-color: rgb(206, 221, 246);
  background-image: url("../images/trash.png");
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: 240px 180px;
}
.functions__content-block:nth-child(2) {
  height: 320px;
  background-color: rgb(231, 238, 250);
}
.functions__content-block:nth-child(3) {
  height: 280px;
  background-color: rgb(206, 221, 246);
  background-image: url("../images/tel.png");
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: 270px;
}
.functions__content-block:nth-child(4) {
  height: 400px;
  background-color: rgb(231, 238, 250);
}
.functions__content-title {
  margin-bottom: 24px;
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 28px;
}
.functions__content-stars {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-top: auto;
}
.functions__content-stars-block {
  display: flex;
  gap: 4px;
}
.functions__content-star {
  width: 32px;
  height: 32px;
}
.functions__content-star.selected path {
  fill: rgb(249, 163, 42);
  fill-opacity: 1;
}
.functions__content-stars-text {
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 28px;
  letter-spacing: 0.1em;
  color: rgba(39, 42, 42, 0.72);
}
.functions__content-price {
  display: flex;
  flex-direction: column;
  margin-top: auto;
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  color: rgba(39, 42, 42, 0.72);
}
.functions__content-price span:first-child {
  text-decoration: line-through;
  font-size: 36px;
  line-height: 48px;
  font-weight: 400;
}
.functions__buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  margin-top: 48px;
}
.functions__buttons-button {
  width: fit-content;
}
.functions__button-label {
  font-size: 14px;
  line-height: 20px;
  color: rgba(39, 42, 42, 0.72);
}

.phone {
  padding-top: 48px;
  background-color: rgb(255, 255, 255);
}
.phone__wrapper {
  overflow: hidden;
  display: grid;
  grid-template-columns: 1fr 426px;
}
.phone__content {
  padding: 0 96px 24px 0;
}
.phone__title {
  margin: 0 0 48px;
