}
.phone__list-item:hover:not(.selected) {
  border-bottom-color: rgba(39, 42, 42, 0.56);
}
.phone__list-item:hover:not(.selected) .phone__list-item-arrow-block {
  background-color: rgba(0, 0, 0, 0.04);
}
.phone__list-item:hover:not(.selected) .phone__list-item-arrow path {
  fill: rgb(255, 107, 0);
}
.phone__list-item.selected {
  border-bottom-color: rgb(255, 107, 0);
}
.phone__list-item.selected .phone__list-item-title {
  font-weight: 700;
}
.phone__list-item-block {
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding: 8px 0;
}
.phone__list-item-title {
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 28px;
}
.phone__list-item-text {
  color: rgba(39, 42, 42, 0.72);
}
.phone__list-item-arrow-block {
  flex-shrink: 0;
  width: 44px;
  height: 44px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
