}
.phone__img-block {
  background-repeat: no-repeat, no-repeat;
  background-position: top, 20px 110px;
  background-size: cover, 90%;
}
.phone__img-block.first {
  background-image: url("../images/phone.png"), url("../images/messages-01.gif");
}
.phone__img-block.second {
  background-image: url("../images/phone.png"), url("../images/messages-02.png");
}
.phone__img-block.third {
  background-image: url("../images/phone.png"), url("../images/messages-02.png");
}
.phone__img-block.four {
  background-image: url("../images/phone.png"), url("../images/messages-02.png");
}

.prices {
  padding: 48px 0;
}
.prices__wrapper {
  display: flex;
  flex-direction: column;
}
.prices__title {
  margin: 0 0 12px;
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 700;
  font-size: 28px;
  line-height: 36px;
  color: rgba(255, 255, 255, 0.92);
  text-align: center;
}
.prices__sub-title {
  margin-bottom: 24px;
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 28px;
  text-align: center;
  color: rgba(255, 255, 255, 0.92);
}
.prices__price-block {
  margin: 24px auto 0;
  width: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.prices__price {
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 300;
  font-size: 48px;
  line-height: 72px;
  color: rgba(255, 255, 255, 0.92);
}
.prices__price span:nth-child(3) {
  font-weight: 400;
  font-size: 24px;
  line-height: 36px;
}
.prices__price-label {
  font-size: 14px;
  line-height: 20px;
  color: rgba(255, 255, 255, 0.92);
}
.prices__buttons {
  width: fit-content;
  margin: 24px auto 96px;
  padding: 48px 0 0;
}
.prices__buttons-label {
  margin-top: 8px;
  font-size: 14px;
  line-height: 20px;
  color: rgba(255, 255, 255, 0.92);
  text-align: center;
}
.prices__steps {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 48px;
  color: rgba(255, 255, 255, 0.92);
}
.prices__step {
  position: relative;
  display: flex;
  flex-direction: column;
}
.prices__step:nth-child(1) {
  padding-top: 96px;
}
.prices__step:nth-child(2) {
  padding-top: 144px;
}
.prices__step:nth-child(2) .prices__step-count {
  top: 48px;
}
.prices__step:nth-child(3) {
  padding-top: 96px;
}
.prices__step-count {
  position: absolute;
  left: 0;
  top: 0;
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 700;
  font-size: 120px;
  line-height: 146px;
  color: rgb(32, 54, 91);
}
.prices__step-title {
  z-index: 20;
  margin-bottom: 12px;
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 28px;
}
.prices__step-icon-1 {
  position: absolute;
  left: 96px;
  top: 32px;
  width: 272px;
  height: 51px;
}
.prices__step-icon-2 {
  position: absolute;
  left: 96px;
  top: 56px;
  width: 274px;
  height: 90px;
}
.prices__step-text {
  z-index: 30;
}
.prices__footer {
  margin-top: 48px;
  margin-bottom: 12px;
  text-align: center;
  font-size: 14px;
  line-height: 20px;
  color: rgba(255, 255, 255, 0.72);
}
.prices__footer a {
  text-decoration: none;
  color: rgb(109, 154, 227);
  transition: color 0.2s;
}
.prices__footer a:hover {
  color: rgb(255, 107, 0);
}

.tabs {
  width: fit-content;
  display: flex;
  padding: 4px;
  margin: 0 auto;
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.24);
}
.tabs__tab {
  width: 168px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  border-radius: 12px;
  background-color: transparent;
  color: rgba(255, 255, 255, 0.92);
  cursor: pointer;
  transition: all 0.2s;
}
.tabs__tab:hover:not(.selected) {
  background-color: rgba(255, 255, 255, 0.04);
}
.tabs__tab.selected {
  background-color: rgb(75, 81, 88);
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.04);
}

.questions {
  padding: 48px 0;
  background-color: rgb(255, 255, 255);
}
.questions__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.questions__block-a {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.questions__title {
  max-width: 480px;
  margin: 0;
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 700;
  font-size: 28px;
  line-height: 36px;
}
.questions__text {
  max-width: 480px;
}
.questions__button {
  height: 48px;
  display: flex;
  align-items: center;
  gap: 8px;
  border: none;
  padding: 0;
  margin: 0;
  background-color: transparent;
  cursor: pointer;
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 600;
  color: rgb(11, 87, 208);
  transition: color 0.2s;
}
.questions__button:hover {
  color: rgb(224, 94, 0);
}
.questions__button:hover .questions__button-icon path {
  fill: rgb(224, 94, 0);
}
.questions__button-icon {
  width: 32px;
  height: 32px;
}
.questions__button-icon path {
  fill: rgb(11, 87, 208);
  transition: fill 0.2s;
}

.accordion {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.accordion__item {
  border-bottom: 1px solid rgba(39, 42, 42, 0.16);
}

.accordion__header {
  min-height: 64px;
  display: flex;
  justify-content: space-between;
  gap: 22px;
  font-family: "Montserrat", "arial", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 28px;
  cursor: pointer;
  transition: all 0.2s;
}
.accordion__header span {
  margin: 12px 0;
}

.accordion__header-icon-wrapper {
  flex-shrink: 0;
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 4px 0;
  border-radius: 12px;
  transition: all 0.2s;
}

.accordion__header-icon {
  width: 22px;
  height: 22px;
  transition: all 0.2s;
}
.accordion__header-icon path {
  fill: rgba(39, 42, 42, 0.72);
  transition: fill 0.2s;
}

.accordion__item_show .accordion__header-icon,
.accordion__item_slidedown .accordion__header-icon {
  transform: rotate(-180deg);
}

.accordion__item_show,
.accordion__item_slidedown {
  border-color: rgb(255, 107, 0);
}

.accordion__item_show .accordion__header,
.accordion__item_slidedown .accordion__header {
  font-weight: 700;
}

.accordion__header:hover {
  border-color: rgba(39, 42, 42, 0.56);
}
.accordion__header:hover .accordion__header-icon-wrapper {
  background-color: rgba(0, 0, 0, 0.04);
}
.accordion__header:hover .accordion__header-icon path {
  fill: rgb(255, 107, 0);
}

.accordion__content {
  margin-bottom: 32px;
  margin-right: 56px;
}

.accordion__item:not(.accordion__item_show) .accordion__body {
  display: none;
}

* {
